import React from "react";
import './App.css';
import './NavLink.js';  
import Search from './component/Example';
import App1 from './component/App1';
import Appupload3 from './component/appupload3';
import App2 from './component/App2';
import App3 from './component/Appviewonly3';
import Search11 from './component/Search';
import Login from './component/login';
import Signup from './component/signup';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  NavLink as RRNavLink
} from "react-router-dom";
//import Example from "./Example";

// This site has 3 pages, all of which are rendered
// dynamically in the browser (not server rendered).
//
// Although the page does not ever refresh, notice how
// React Router keeps the URL up to date as you navigate
// through the site. This preserves the browser history,
// making sure things like the back button and bookmarks
// work properly.
import { useState } from 'react';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  //NavbarText,
} from 'reactstrap';





export default function BasicExample() {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);
  return (
    <Router>
      <div>
      <Navbar color="light" light expand="md"> 
        <NavbarBrand href="/"><h1>EasyGo</h1></NavbarBrand>
        <NavbarToggler onClick={toggle} />
        <Collapse isOpen={isOpen} navbar>
          <Nav  className="mr-auto" navbar>
            <NavItem>
            <NavLink className="nav-link" activeClassName = 'active-link' exact to="/"  tag={RRNavLink}>Home</NavLink>
            </NavItem>
            <NavItem>
            <NavLink tag={RRNavLink} className="nav-link" to="/accounts" activeClassName="active-link" >Accounts</NavLink>
            </NavItem>
            <UncontrolledDropdown nav inNavbar>
              <DropdownToggle nav caret>
                Sales
              </DropdownToggle>
              <DropdownMenu right>
                <DropdownItem>
                <NavLink exact to="/suppliers" tag={RRNavLink}>About1</NavLink>
                </DropdownItem>
                <DropdownItem>
                <NavLink exact to="/suppliers" tag={RRNavLink}>About2</NavLink>
                </DropdownItem>
                <DropdownItem divider />
                <DropdownItem>
                <NavLink exact to="/" tag={RRNavLink}>About3</NavLink>
                </DropdownItem>
              </DropdownMenu>
            </UncontrolledDropdown>
            <NavItem >
            <NavLink exact to="/" className="inactive" activeClassName="active" tag={RRNavLink} >Dashboard</NavLink>
            </NavItem>
            <NavItem>
            <NavLink to="/" style={{ textDecoration: 'none'}} activeStyle={{color: 'red', fontWeight: "bold",textDecoration: 'none'}} tag={RRNavLink}>Home</NavLink>
            </NavItem>
            <UncontrolledDropdown nav inNavbar>
              <DropdownToggle nav caret>
                Accounts
              </DropdownToggle>
              <DropdownMenu right>
                <DropdownItem>
                <NavLink exact to="/suppliers" tag={RRNavLink}>About1</NavLink>
                </DropdownItem>
                <DropdownItem>
                <NavLink exact to="/suppliers" tag={RRNavLink}>About1</NavLink>
                </DropdownItem>
                <DropdownItem divider />
                <DropdownItem>
                <NavLink exact to="/suppliers" tag={RRNavLink}>About1</NavLink>
                </DropdownItem>
              </DropdownMenu>
            </UncontrolledDropdown>
            <NavItem>
            <NavLink exact to="/suppliers" activeClassName = 'active-link' tag={RRNavLink}>Warehouse</NavLink>
            </NavItem>
            
            <UncontrolledDropdown nav inNavbar>
              <DropdownToggle nav caret>
                Setting
              </DropdownToggle>
              <DropdownMenu right>
                <DropdownItem>
                <NavLink exact to="/suppliers" tag={RRNavLink}>About1</NavLink>
                </DropdownItem>
                <DropdownItem>
                <NavLink exact to="/suppliers" tag={RRNavLink}>About1</NavLink>
                </DropdownItem>
                <DropdownItem divider />
                <DropdownItem>
                <NavLink exact to="/accounts" tag={RRNavLink}>Report</NavLink>
                </DropdownItem>
              </DropdownMenu>
            </UncontrolledDropdown>
            <UncontrolledDropdown nav inNavbar>
              <DropdownToggle nav caret>
                User
              </DropdownToggle>
              <DropdownMenu right>
                <DropdownItem>
                <NavLink exact to="/login" tag={RRNavLink}>Sign in</NavLink>
                </DropdownItem>
                <DropdownItem>
                <NavLink exact to="/signup" tag={RRNavLink}>Sign Up </NavLink>
                </DropdownItem>
                <DropdownItem divider />
                <DropdownItem>
                <NavLink exact to="/accounts" tag={RRNavLink}>Report</NavLink>
                </DropdownItem>
              </DropdownMenu>
            </UncontrolledDropdown>
          </Nav>
          Simple Text
        </Collapse>
      </Navbar>
    </div>
      <div>
        

        <hr />

        {/*
          A <Switch> looks through all its children <Route>
          elements and renders the first one whose path
          matches the current URL. Use a <Switch> any time
          you have multiple routes, but you want only one
          of them to render at a time
        */}
        <Switch>
          <Route exact path="/">
            <Home />
          </Route>
          <Route path="/accounts">
          <About />
          </Route>
          <Route path="/suppliers">
            <Dashboard />
          </Route>
          <Route path="/suppliers">
            <Dashboard />
            </Route>
          <Route path="/suppliers">
            <Dashboard />
            </Route><Route path="/login">
            <Loginm />
          </Route>
          
          <Route path="/signup">
            <Signupm />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

// You can think of these components as "pages"
// in your app.

function Home() {
  return (
    <div>
 <App3 />
    </div>
  );
}

function About() {
  return (
    <div>
       
       <Appupload3 />
      <h2>About roy</h2>
    </div>
  );
}

function Dashboard() {
  return (
    <div>
     <App2 />

    </div>
  );
}

function Signupm() {
  return (
    <div>
     <Signup />

    </div>
  );
}

function Loginm() {
  return (
    <div>
     <Login />

    </div>
  );
}
const style = {
  color: 'blue',
  "a:active": {
    color: 'yellow'
  }
};
